'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { useTheme } from 'next-themes';
import {
    Shield,
    Moon,
    Sun,
    Server,
    Terminal,
    Info
} from 'lucide-react';
import Navigation from '../components/Navigation';

export default function ProfilePage() {
    const router = useRouter();
    const { theme, setTheme } = useTheme();
    const [mounted, setMounted] = useState(false);

    // Hardcoded profile for display - driven by env vars in real app
    const profile = {
        name: 'DevOps Admin',
        email: 'admin@naratel.com',
        role: 'Administrator'
    };

    useEffect(() => {
        setMounted(true);
    }, []);

    const handlePageChange = (page) => {
        if (page === 'jenkins') {
            router.push('/');
        } else if (page === 'k8s-secret') {
            router.push('/secrets');
        } else if (page === 'manifest') {
            router.push('/manifest');
        } else if (page === 'profile') {
            router.push('/profile');
        }
    };

    if (!mounted) return null;

    return (
        <div className="min-h-screen bg-gray-50 dark:bg-neutral-950 transition-colors">
            <Navigation activePage="profile" onPageChange={handlePageChange} />

            <main className="md:ml-56 min-h-screen text-neutral-900 dark:text-white relative overflow-hidden">
                {/* Background Decoration */}
                <div className="absolute top-0 left-0 w-full h-full overflow-hidden pointer-events-none">
                    <div className="absolute top-[-10%] right-[-5%] w-96 h-96 bg-[#FFA500]/5 rounded-full blur-3xl"></div>
                    <div className="absolute bottom-[-10%] left-[-5%] w-96 h-96 bg-blue-500/5 rounded-full blur-3xl"></div>
                </div>

                <div className="max-w-4xl mx-auto relative z-10 p-6 md:p-12">

                    {/* Header */}
                    <div className="mb-8">
                        <h1 className="text-3xl font-bold tracking-tight mb-2">System Settings</h1>
                        <p className="text-neutral-500 dark:text-neutral-400">View session information and client preferences</p>
                    </div>

                    <div className="grid grid-cols-1 md:grid-cols-2 gap-8">

                        {/* Left Column: Session Info */}
                        <div className="space-y-6">
                            {/* Session Identity Card */}
                            <div className="bg-white dark:bg-neutral-900/50 backdrop-blur-sm border border-neutral-200 dark:border-neutral-800 rounded-2xl p-6 shadow-sm">
                                <div className="flex items-center gap-3 mb-6">
                                    <div className="p-2 bg-[#FFA500]/10 rounded-lg">
                                        <Shield className="text-[#FFA500]" size={20} />
                                    </div>
                                    <h2 className="text-lg font-bold">Current Session</h2>
                                </div>

                                <div className="flex items-center gap-4 p-4 rounded-xl bg-gray-50 dark:bg-neutral-950 border border-neutral-200 dark:border-neutral-800">
                                    <div className="w-12 h-12 bg-gradient-to-br from-[#FFA500] to-orange-600 rounded-full flex items-center justify-center text-white font-bold text-xl shadow-md">
                                        {profile.name.charAt(0)}
                                    </div>
                                    <div>
                                        <p className="font-bold text-lg leading-tight">{profile.name}</p>
                                        <p className="text-sm text-neutral-500">{profile.email}</p>
                                    </div>
                                </div>

                                <div className="mt-6 space-y-3">
                                    <div className="flex justify-between text-sm">
                                        <span className="text-neutral-500">Access Level</span>
                                        <span className="font-mono font-bold text-[#FFA500] bg-[#FFA500]/10 px-2 py-0.5 rounded textxs">ROOT_ADMIN</span>
                                    </div>
                                    <div className="flex justify-between text-sm">
                                        <span className="text-neutral-500">Auth Method</span>
                                        <span className="font-medium">Credentials (Env)</span>
                                    </div>
                                </div>
                            </div>

                            {/* Configuration Info */}
                            <div className="bg-blue-50 dark:bg-blue-500/5 border border-blue-200 dark:border-blue-500/20 rounded-2xl p-6">
                                <div className="flex items-start gap-3">
                                    <Info className="text-blue-500 shrink-0 mt-0.5" size={20} />
                                    <div>
                                        <h3 className="font-bold text-blue-700 dark:text-blue-400 mb-1">Infrastructure as Code</h3>
                                        <p className="text-sm text-blue-600 dark:text-blue-400/80 leading-relaxed">
                                            Account credentials and roles are managed via environment variables to ensure state consistency across Kubernetes pods.
                                        </p>
                                        <div className="mt-3 flex items-center gap-2 text-xs font-mono bg-white/50 dark:bg-black/20 p-2 rounded border border-blue-200 dark:border-blue-500/20 text-blue-800 dark:text-blue-300">
                                            <Terminal size={12} />
                                            <span>NEXTAUTH_SECRET=********</span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        {/* Right Column: Preferences */}
                        <div className="space-y-6">
                            <div className="bg-white dark:bg-neutral-900/50 backdrop-blur-sm border border-neutral-200 dark:border-neutral-800 rounded-2xl p-6 shadow-sm h-full">
                                <div className="flex items-center gap-3 mb-6">
                                    <div className="p-2 bg-purple-500/10 rounded-lg">
                                        <Moon className="text-purple-500" size={20} />
                                    </div>
                                    <h2 className="text-lg font-bold">Preferences</h2>
                                </div>

                                <div className="space-y-6">
                                    {/* Theme Toggle */}
                                    <div className="p-4 rounded-xl bg-gray-50 dark:bg-neutral-950 border border-neutral-200 dark:border-neutral-800">
                                        <div className="flex items-center justify-between mb-3">
                                            <span className="font-bold text-sm">Appearance</span>
                                            {theme === 'dark' ? <Moon size={18} className="text-purple-400" /> : <Sun size={18} className="text-[#FFA500]" />}
                                        </div>
                                        <div className="grid grid-cols-2 gap-2">
                                            <button
                                                onClick={() => setTheme('light')}
                                                className={`px-3 py-2 rounded-lg text-sm font-medium transition-all ${theme === 'light'
                                                        ? 'bg-white text-neutral-900 shadow-sm ring-1 ring-neutral-200'
                                                        : 'text-neutral-500 hover:bg-white/50'
                                                    }`}
                                            >
                                                Light
                                            </button>
                                            <button
                                                onClick={() => setTheme('dark')}
                                                className={`px-3 py-2 rounded-lg text-sm font-medium transition-all ${theme === 'dark'
                                                        ? 'bg-neutral-800 text-white shadow-sm ring-1 ring-neutral-700'
                                                        : 'text-neutral-500 hover:bg-neutral-800/50'
                                                    }`}
                                            >
                                                Dark
                                            </button>
                                        </div>
                                    </div>

                                    <div className="p-4 rounded-xl bg-gray-50 dark:bg-neutral-950 border border-neutral-200 dark:border-neutral-800">
                                        <div className="flex items-center gap-3 mb-2">
                                            <Server size={16} className="text-neutral-500" />
                                            <span className="font-bold text-sm">System Status</span>
                                        </div>
                                        <div className="space-y-2">
                                            <div className="flex justify-between text-xs">
                                                <span className="text-neutral-500">Environment</span>
                                                <span className="text-green-500 font-medium">Production</span>
                                            </div>
                                            <div className="flex justify-between text-xs">
                                                <span className="text-neutral-500">Version</span>
                                                <span className="text-neutral-900 dark:text-white font-mono">v1.2.0</span>
                                            </div>
                                        </div>
                                    </div>

                                </div>
                            </div>
                        </div>

                    </div>
                </div>
            </main>
        </div>
    );
}
